import React from "react";
import "hui/message";

export default class HAMessage extends React.Component {
    static propTypes = {
        className: React.PropTypes.string
    };

    constructor(props) {
        super(props);
    }

    render() {
        /* jshint ignore:start */
        return (
            <ha-message class={this.props.className} {...this.props}>
                {this.props.children}
            </ha-message>
        );
        /* jshint ignore:end */
    }
}
